<template>
    <v-dialog v-model="dialog" width="700px">
          <template v-slot:activator="{ on, attrs }">
            <v-btn color="primary" dark class="mb-2" v-bind="attrs" v-on="on">
              New Item
            </v-btn>
          </template>
          <v-card>
            <v-card-title>
              <span class="text-h5">{{ message }}</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-row >
                  <v-col cols="12" sm="6" md="12">
                    <v-text-field v-model="editedItem.Nom" label="Nom"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="12">
                    <v-text-field v-model="editedItem.Prenom" label="Prenom"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="12">
                    <v-text-field v-model="editedItem.Age" label="Age"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="12">
                    <!-- <v-text-field v-model="editedItem.Orphelin" label="Orphelin"></v-text-field> -->
                    <span>Orphan :</span>
                     <div class="checkbox-wrapper-7">
                            <input class="tgl tgl-ios" id="cb2-7" type="checkbox" @click="takeValue"/>
                            <label class="tgl-btn" for="cb2-7"></label>
                    </div>

                  </v-col>
                  <v-col cols="12" sm="6" md="12">
                    <v-text-field v-model="editedItem.Filiere" label="Filiere"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="12">
                    <v-text-field v-model="editedItem.Niveau" label="Niveau"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="12">
                    <v-text-field v-model="editedItem.sexe" label="sexe"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="12">
                    <v-text-field v-model="editedItem.matricule" label="matricule"></v-text-field>
                  </v-col>
                  <v-col>
                    <v-file-input @change="selectFile" show-size counter multiple label="Select Image" filled prepend-icon="mdi-camera"></v-file-input>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="close">
                Cancel
              </v-btn>
              <v-btn color="blue darken-1" text @click="save">
                Save
              </v-btn>
            </v-card-actions>
          </v-card>
    </v-dialog>
</template>

<script>
import { boolean } from "webidl-conversions";
import API from "../api";


 export default {
    props:{
        message: {
            type:String,
            required:true
        }
    },
    data (){
        return {
            dialog: false,
            editedItem: {
                Nom: '',
                Prenom: '',
                Age: 0,
                Filiere: '',
                Niveau: 0,
                sexe: '',
                matricule: '',
            },
            image:'',
            Orphelin: false,
            alertMessage:''
        }
    },

  methods:{

    takeValue(){
     const check = document.querySelector('#cb2-7');
     console.log(check.checked);
     this.Orphelin = check.checked
    },
    selectFile(file){
                this.image = file[0].name
                console.log('@@@ this.image',this.image);
            },

      async save(){

     var data ={
        name : this.editedItem.Nom,
        firstName : this.editedItem.Prenom,
        age : this.editedItem.Age,
        sexe  : this.editedItem.sexe,
        orphan  : this.Orphelin,
        filiere : this.editedItem.Filiere,
        niveau  : this.editedItem.Niveau,
        matricule   : this.editedItem.matricule,
        image  : this.image
    }
     var res = await API.createStudent(data);
     console.log('@@@@res',res);
     this.alertMessage = res.message;
     this.$emit('alert',this.alertMessage);
     console.log('Students create Succesfully');
     this.close();
      },

      close(){
        this.dialog = false
        this.editedItem = {}
      }
    },
    emits:['alert'],
 }
</script>

<style>
  .checkbox-wrapper-7 .tgl {
    display: none;
  }
  .checkbox-wrapper-7 .tgl,
  .checkbox-wrapper-7 .tgl:after,
  .checkbox-wrapper-7 .tgl:before,
  .checkbox-wrapper-7 .tgl *,
  .checkbox-wrapper-7 .tgl *:after,
  .checkbox-wrapper-7 .tgl *:before,
  .checkbox-wrapper-7 .tgl + .tgl-btn {
    box-sizing: border-box;
  }
  .checkbox-wrapper-7 .tgl::-moz-selection,
  .checkbox-wrapper-7 .tgl:after::-moz-selection,
  .checkbox-wrapper-7 .tgl:before::-moz-selection,
  .checkbox-wrapper-7 .tgl *::-moz-selection,
  .checkbox-wrapper-7 .tgl *:after::-moz-selection,
  .checkbox-wrapper-7 .tgl *:before::-moz-selection,
  .checkbox-wrapper-7 .tgl + .tgl-btn::-moz-selection,
  .checkbox-wrapper-7 .tgl::selection,
  .checkbox-wrapper-7 .tgl:after::selection,
  .checkbox-wrapper-7 .tgl:before::selection,
  .checkbox-wrapper-7 .tgl *::selection,
  .checkbox-wrapper-7 .tgl *:after::selection,
  .checkbox-wrapper-7 .tgl *:before::selection,
  .checkbox-wrapper-7 .tgl + .tgl-btn::selection {
    background: none;
  }
  .checkbox-wrapper-7 .tgl + .tgl-btn {
    outline: 0;
    display: block;
    width: 4em;
    height: 2em;
    position: relative;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  .checkbox-wrapper-7 .tgl + .tgl-btn:after,
  .checkbox-wrapper-7 .tgl + .tgl-btn:before {
    position: relative;
    display: block;
    content: "";
    width: 50%;
    height: 100%;
  }
  .checkbox-wrapper-7 .tgl + .tgl-btn:after {
    left: 0;
  }
  .checkbox-wrapper-7 .tgl + .tgl-btn:before {
    display: none;
  }
  .checkbox-wrapper-7 .tgl:checked + .tgl-btn:after {
    left: 50%;
  }

  .checkbox-wrapper-7 .tgl-ios + .tgl-btn {
    background: #fbfbfb;
    border-radius: 2em;
    padding: 2px;
    transition: all 0.4s ease;
    border: 1px solid #e8eae9;
  }
  .checkbox-wrapper-7 .tgl-ios + .tgl-btn:after {
    border-radius: 2em;
    background: #fbfbfb;
    transition: left 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275), padding 0.3s ease, margin 0.3s ease;
    box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1), 0 4px 0 rgba(0, 0, 0, 0.08);
  }
  .checkbox-wrapper-7 .tgl-ios + .tgl-btn:hover:after {
    will-change: padding;
  }
  .checkbox-wrapper-7 .tgl-ios + .tgl-btn:active {
    box-shadow: inset 0 0 0 2em #e8eae9;
  }
  .checkbox-wrapper-7 .tgl-ios + .tgl-btn:active:after {
    padding-right: 0.8em;
  }
  .checkbox-wrapper-7 .tgl-ios:checked + .tgl-btn {
    background: #86d993;
  }
  .checkbox-wrapper-7 .tgl-ios:checked + .tgl-btn:active {
    box-shadow: none;
  }
  .checkbox-wrapper-7 .tgl-ios:checked + .tgl-btn:active:after {
    margin-left: -0.8em;
  }
</style>
